import React from "react";
import { NavLink } from "react-router-dom";

const SidebarSingleItem = ({ label, icon, ...rest }) => {
  return (
    <li className="nav-item">
      <NavLink {...rest} className="nav-link primary-sidebar-link py-3">
        <i className={icon}></i>
        <span>{label}</span>
      </NavLink>
    </li>
  );
};

export default SidebarSingleItem;
