import jwtDecode from "jwt-decode";
import http from "./httpService";
import { apiUrl } from "../config.json";

const accessToken = "accessToken";
const refreshToken = "refreshToken";

http.setJwt(getJwt());

export async function login(username, password) {
  const apiEndPoint = apiUrl + "/api/login/";
  const tokens = await http.post(apiEndPoint, { username, password });
  localStorage.setItem(accessToken, tokens.data.access);
  localStorage.setItem(refreshToken, tokens.data.refresh);
}
export async function register(data) {
  const {
    firstName,
    lastName,
    codeMeli,
    shomareShenasname,
    homeAddress,
    email,
    homePostalCode,
    homePhoneNumber,
    phoneNumber,
    fatherFirstName,
    fatherPhoneNumber,
    fatherCodeMeli,
    fatherJobName,
    fatherWorkAddress,
    fatherWorkPostalCode,
    fatherWorkPhoneNumber,
    motherFirstName,
    motherPhoneNumber,
    motherCodeMeli,
    motherJobName,
    motherWorkAddress,
    motherWorkPostalCode,
    motherWorkPhoneNumber,
    cartMeliImg,
    personalImg,
    shenasnameImg,
    gender,
    nation,
    dayOfBirth,
    monthOfBirth,
    yearOfBirth,
    role,
    personalCode,
    birthLocation,
    religion,
    section,
  } = data;
  const apiEndPoint = apiUrl + "/Users/App/regiseter_user/";
  const user = {
    first_name: firstName,
    last_name: lastName,
    email: email,
    role: role,
    religion: religion,
    userPhoto: personalImg,
    userNationalCardPhoto: shenasnameImg,
    userIdCardPhoto: cartMeliImg,
    user_pNum: phoneNumber,
    home_pNum: homePhoneNumber,
    address: homeAddress,
    zipCode: homePostalCode,
    personalCode: personalCode,
    nationalCode: codeMeli,
    father_nationalCode: fatherCodeMeli,
    father_name: fatherFirstName,
    father_pNum: fatherPhoneNumber,
    father_jobName: fatherJobName,
    father_jobAddress: fatherWorkAddress,
    father_job_postalCode: fatherWorkPostalCode,
    father_job_pNum: fatherWorkPhoneNumber,
    mother_nationalCode: motherCodeMeli,
    mother_name: motherFirstName,
    mother_pNum: motherPhoneNumber,
    mother_jobName: motherJobName,
    mother_jobAddress: motherWorkAddress,
    mother_job_postalCode: motherWorkPostalCode,
    mother_job_pNum: motherWorkPhoneNumber,
    citizen_Num: shomareShenasname,
    date_of_birth: `${yearOfBirth}/${monthOfBirth}/${dayOfBirth}`,
    place_of_birth: birthLocation,
    citizen: nation,
    gender: gender,
    section: section,
  };
  const s = await http.post(apiEndPoint, user);
  console.log(s);
}
export function logout() {
  localStorage.removeItem(accessToken);
  localStorage.removeItem(refreshToken);
}
export function getCurrentUser() {
  try {
    const token = localStorage.getItem(accessToken);
    const { user_id: userId } = jwtDecode(token);

    return userId;
  } catch (ex) {
    return null;
  }
}
export async function getUserProfile(userId) {
  const apiEndPoint = `${apiUrl}/Users/App/user/profile/?user_id=${userId}`;
  try {
    const userProfile = await http.get(apiEndPoint);
    return userProfile.data;
  } catch (ex) {
    logout();
    http.toast.error("شما نیاز به لاگین کردن دارید");
    //console.log(ex.response);
    return null;
  }
}
export async function uploadImage(file) {
  //fix this for errors
  const userId = getCurrentUser();
  const uploadData = new FormData();
  uploadData.append("user_id", userId);
  uploadData.append("image", file, file.name);
  const img = await http.post(apiUrl + "/Users/App/images/api/", uploadData);
  return img;
}
export function getJwt() {
  return localStorage.getItem(accessToken);
}
export default {
  login,
  logout,
  getCurrentUser,
  getJwt,
  uploadImage,
  register,
  getUserProfile,
  apiUrl,
};
