import React from "react";
import { Link, NavLink } from "react-router-dom";
const SidebarMultiItem = ({ label, icon, targetId, items }) => {
  return (
    <li className="nav-item">
      <div className="d-flex align-items-center collapse-menu">
        <Link
          to=""
          className="nav-link primary-sidebar-link w-100 py-3 collapsed"
          data-toggle="collapse"
          data-target={`#${targetId}`}
        >
          <i className={icon}></i>
          <span>{label}</span>
        </Link>
      </div>
      <div className="collapse collapse-menu" id={targetId}>
        <ul className="nav flex-column px-3 small">
          {items.map((item) => (
            <li key={item.label} className="nav-item">
              <NavLink to={item.to} className="nav-link pr-2">
                {item.label}
              </NavLink>
            </li>
          ))}
        </ul>
      </div>
      <div className="dropleft sidebarDropdown">
        <Link
          to=""
          className="nav-link primary-sidebar-link w-100 py-3"
          id="dropdownMenuButton"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          <i className={icon}></i>
        </Link>
        <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
          <ul className="nav flex-column px-3 small">
            {items.map((item) => (
              <li key={item.label} className="nav-item">
                <NavLink to={item.to} className="nav-link pr-2">
                  {item.label}
                </NavLink>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </li>
  );
};

export default SidebarMultiItem;
