import React from "react";
import { toast } from "react-toastify";
import axios from "axios";
import Joi from "joi-browser";
import Form from "../common/form";
import auth from "../../services/authService";
import logo from "../../Student-Default-Icon.png";
class RegisterForm extends Form {
  state = {
    uploadData: {
      cartMeliImg: { uploadPercentage: "", file: "", cancelToken: "" },
      personalImg: { uploadPercentage: "", file: "", cancelToken: "" },
      shenasnameImg: { uploadPercentage: "", file: "", cancelToken: "" },
    },
    data: {
      firstName: "",
      lastName: "",
      codeMeli: "",
      shomareShenasname: "",
      homeAddress: "",
      email: "",
      homePostalCode: "",
      homePhoneNumber: "",
      phoneNumber: "",
      fatherFirstName: "",
      fatherPhoneNumber: "",
      fatherCodeMeli: "",
      fatherJobName: "",
      fatherWorkAddress: "",
      fatherWorkPostalCode: "",
      fatherWorkPhoneNumber: "",
      motherFirstName: "",
      motherPhoneNumber: "",
      motherCodeMeli: "",
      motherJobName: "",
      motherWorkAddress: "",
      motherWorkPostalCode: "",
      motherWorkPhoneNumber: "",
      cartMeliImg: "",
      personalImg: "",
      shenasnameImg: "",
      gender: "1",
      nation: "1",
      dayOfBirth: "",
      monthOfBirth: "",
      yearOfBirth: "",
      role: "4",
      personalCode: "0",
      birthLocation: "",
      religion: "",
      section: "0",
    },
    errors: {},
    section_choices: [
      { name: "پیش دبستانی", _id: "0" },
      { name: "اول", _id: "1" },
      { name: "دوم", _id: "2" },
      { name: "سوم", _id: "3" },
      { name: "چهارم", _id: "4" },
      { name: "پنجم", _id: "5" },
      { name: "ششم", _id: "6" },
      { name: "هفتم", _id: "7" },
      { name: "هشتم", _id: "8" },
      { name: "نهم", _id: "9" },
    ],
  };

  schema = {
    firstName: Joi.string()
      .required()
      .label("نام")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .error((errors) => this.ErrorsLang(errors)),
    lastName: Joi.string()
      .required()
      .label("نام خانوادگی")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .error((errors) => this.ErrorsLang(errors)),
    codeMeli: Joi.string()
      .required()
      .label("کدملی")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .error((errors) => this.ErrorsLang(errors)),
    shomareShenasname: Joi.string()
      .required()
      .label("شماره شناسنامه")
      .regex(/^[0-9]+$/, "عدد")
      .min(2)
      .max(10)
      .error((errors) => this.ErrorsLang(errors)),
    homeAddress: Joi.string()
      .required()
      .label("آدرس منزل")
      .error((errors) => this.ErrorsLang(errors)),
    email: Joi.string()
      .email()
      .required()
      .label("پست الکترونیک")
      .error((errors) => this.ErrorsLang(errors)),
    homePostalCode: Joi.string()
      .required()
      .label("کدپستی منزل")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .error((errors) => this.ErrorsLang(errors)),
    homePhoneNumber: Joi.string()
      .required()
      .label("شماره منزل")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .error((errors) => this.ErrorsLang(errors)),
    phoneNumber: Joi.string()
      .required()
      .label("شماره همراه")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .error((errors) => this.ErrorsLang(errors)),
    fatherFirstName: Joi.string()
      .label("نام پدر")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherPhoneNumber: Joi.string()
      .label("شماره تماس همراه پدر")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherCodeMeli: Joi.string()
      .label("کد ملی پدر")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherJobName: Joi.string()
      .label("شغل پدر")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherWorkAddress: Joi.string()
      .label("آدرس محل کار پدر")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherWorkPostalCode: Joi.string()
      .label("کدپستی محل کار پدر")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    fatherWorkPhoneNumber: Joi.string()
      .label("شماره تماس محل کار پدر")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherFirstName: Joi.string()
      .label("نام مادر")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherPhoneNumber: Joi.string()
      .label("شماره تماس همراه مادر")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherCodeMeli: Joi.string()
      .label("کد ملی مادر")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherJobName: Joi.string()
      .label("شغل مادر")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherWorkAddress: Joi.string()
      .label("آدرس محل کار مادر")
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherWorkPostalCode: Joi.string()
      .label("کدپستی محل کار مادر")
      .regex(/^[0-9]+$/, "عدد")
      .min(10)
      .max(10)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    motherWorkPhoneNumber: Joi.string()
      .label("شماره تماس محل کار مادر")
      .regex(/^[0-9]+$/, "عدد")
      .min(11)
      .max(11)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
    gender: Joi.any(),
    nation: Joi.any(),
    role: Joi.any(),
    cartMeliImg: Joi.any(),
    personalImg: Joi.any(),
    shenasnameImg: Joi.any(),
    dayOfBirth: Joi.number()
      .required()
      .label("روز تولد")
      .max(31)
      .min(1)
      .error((errors) => this.ErrorsLang(errors)),
    monthOfBirth: Joi.number()
      .required()
      .label("ماه تولد")
      .max(12)
      .min(1)
      .error((errors) => this.ErrorsLang(errors)),
    yearOfBirth: Joi.number()
      .required()
      .label("سال تولد")
      .max(92)
      .min(60)
      .error((errors) => this.ErrorsLang(errors)),
    personalCode: Joi.string()
      .required()
      .label("کد پرسنلی")
      .regex(/^[0-9]+$/, "عدد")
      .error((errors) => this.ErrorsLang(errors)),
    birthLocation: Joi.string()
      .required()
      .label("محل تولد")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .error((errors) => this.ErrorsLang(errors)),
    religion: Joi.string()
      .required()
      .label("مذهب")
      .regex(/^[\u0600-\u06FF\s]+$/, "حروف فارسی")
      .error((errors) => this.ErrorsLang(errors)),
    section: Joi.string()
      .required()
      .label("مقطع")
      .error((errors) => this.ErrorsLang(errors)),
  };

  doSubmit = async () => {
    try {
      const { data } = this.state;
      await auth.register(data);
      toast.success("کاربر با موفقیت ثبت شد.");
    } catch (ex) {
      toast.error("کاربر مورد نظر ثبت نشد.");
    }
  };
  uploadCartMeliImg = (e) => {
    const uploadData = { ...this.state.uploadData };
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    uploadData[e.currentTarget.name].file = e.target.files[0]; // should be in the IF ?
    if (e.target.files[0])
      uploadData[e.currentTarget.name].file.cancelToken = source;
    this.setState({ uploadData });
    let data = new FormData();
    this.setState({ uploadData });
    const user_id = auth.getCurrentUser();
    data.append("user_id", user_id); //
    data.append("image", e.target.files[0], e.target.files[0].name); //EDIT
    const options = {
      cancelToken: source.token,
      onUploadProgress: (progressEvent) => {
        const { loaded, total } = progressEvent;
        let percent = Math.floor((loaded * 100) / total);
        console.log(`${loaded}kb of ${total}kb | ${percent}%`);
        if (percent < 100) {
          const uploadData = { ...this.state.uploadData };
          uploadData.cartMeliImg.uploadPercentage = percent;
          this.setState({ uploadData });
        }
      },
    };
    axios
      .post(
        "https://myrest-novinschool.fandogh.cloud/Users/App/images/api/",
        data,
        options
      )
      .then((res) => {
        //console.log("error", res);
        const uploadData = { ...this.state.uploadData };
        const data = { ...this.state.data };

        data.cartMeliImg = "/media/" + res.data.data;
        uploadData.cartMeliImg.uploadPercentage = 100;
        toast.success("عکس کارت ملی آپلود شد");
        this.setState({ data, uploadData }, () => {
          setTimeout(() => {
            const uploadData = { ...this.state.uploadData };
            uploadData.cartMeliImg.uploadPercentage = 0;
            this.setState({ uploadData });
          }, 1000);
        });
      })
      .catch((thrown) => {
        toast.error("عکس کارت ملی آپلود نشد.");
        // console.log(thrown);
        const uploadData = { ...this.state.uploadData };
        uploadData.cartMeliImg.uploadPercentage = 0;
        this.setState({ uploadData });
      });
  };
  uploadPersonalImg = (e) => {
    const uploadData = { ...this.state.uploadData };
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    uploadData[e.currentTarget.name].file = e.target.files[0];
    if (e.target.files[0])
      uploadData[e.currentTarget.name].file.cancelToken = source;
    this.setState({ uploadData });
    let data = new FormData();
    this.setState({ uploadData });
    const user_id = auth.getCurrentUser();
    data.append("user_id", user_id); //
    data.append("image", e.target.files[0], e.target.files[0].name); //EDIT
    const options = {
      cancelToken: source.token,
      onUploadProgress: (progressEvent) => {
        const { loaded, total } = progressEvent;
        let percent = Math.floor((loaded * 100) / total);
        // console.log(`${loaded}kb of ${total}kb | ${percent}%`);
        if (percent < 100) {
          const uploadData = { ...this.state.uploadData };
          uploadData.personalImg.uploadPercentage = percent;
          this.setState({ uploadData });
        }
      },
    };
    axios
      .post(
        "https://myrest-novinschool.fandogh.cloud/Users/App/images/api/",
        data,
        options
      )
      .then((res) => {
        //console.log("error", res);
        const uploadData = { ...this.state.uploadData };
        const data = { ...this.state.data };

        data.personalImg = "/media/" + res.data.data;
        uploadData.personalImg.uploadPercentage = 100;
        toast.success("عکس 3x4 آپلود شد");
        this.setState({ data, uploadData }, () => {
          setTimeout(() => {
            const uploadData = { ...this.state.uploadData };
            uploadData.personalImg.uploadPercentage = 0;
            this.setState({ uploadData });
          }, 1000);
        });
      })
      .catch((thrown) => {
        toast.error("عکس 3x4 آپلود نشد.");
        const uploadData = { ...this.state.uploadData };
        uploadData.cartMeliImg.uploadPercentage = 0;
        this.setState({ uploadData });
      });
  };
  uploadShenasnameImg = (e) => {
    const uploadData = { ...this.state.uploadData };
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();
    uploadData[e.currentTarget.name].file = e.target.files[0];
    if (e.target.files[0])
      uploadData[e.currentTarget.name].file.cancelToken = source;
    this.setState({ uploadData });
    let data = new FormData();
    this.setState({ uploadData });
    const user_id = auth.getCurrentUser();
    data.append("user_id", user_id); //
    data.append("image", e.target.files[0], e.target.files[0].name); //EDIT
    const options = {
      cancelToken: source.token,
      onUploadProgress: (progressEvent) => {
        const { loaded, total } = progressEvent;
        let percent = Math.floor((loaded * 100) / total);
        console.log(`${loaded}kb of ${total}kb | ${percent}%`);
        if (percent < 100) {
          const uploadData = { ...this.state.uploadData };
          uploadData.shenasnameImg.uploadPercentage = percent;
          this.setState({ uploadData });
        }
      },
    };
    axios
      .post(
        "https://myrest-novinschool.fandogh.cloud/Users/App/images/api/",
        data,
        options
      )
      .then((res) => {
        //console.log("error", res);
        const uploadData = { ...this.state.uploadData };
        const data = { ...this.state.data };
        data.shenasnameImg = "/media/" + res.data.data;
        uploadData.shenasnameImg.uploadPercentage = 100;
        toast.success("عکس شناسنامه آپلود شد");
        this.setState({ data, uploadData }, () => {
          setTimeout(() => {
            const uploadData = { ...this.state.uploadData };
            uploadData.shenasnameImg.uploadPercentage = 0;
            this.setState({ uploadData });
          }, 1000);
        });
      })
      .catch((thrown) => {
        toast.error("عکس شناسنامه آپلود نشد.");
        const uploadData = { ...this.state.uploadData };
        uploadData.shenasnameImg.uploadPercentage = 0;
        this.setState({ uploadData });
      });
  };
  cancelImgUpload = (name) => {
    const uploadData = { ...this.state.uploadData };
    uploadData[name].file.cancelToken.cancel();
    const CancelToken = axios.CancelToken;
    uploadData[name].cancelToken = CancelToken.source();
    uploadData[name].uploadPercentage = "";
    uploadData[name].file = "";
    this.setState({ uploadData });
    document.getElementById("cartMeliImg").value = ""; // for real? :D
  };
  render() {
    const { personalImg } = this.state.data;
    return (
      <div className="card col-12">
        <div className="card-body">
          <div className="d-flex align-items-center justify-content-center mb-3">
            <div className="text-center">
              <img
                src={personalImg ? auth.apiUrl + personalImg : logo}
                alt="Student-Logo"
                className="rounded-circle"
                height="180px"
              />
              <h6 className="text-center pt-3">ثبت کاربر جدید</h6>
              <span>اطلاعات فرم را وارد کنید</span>
              <p className="small pt-2 text-danger">
                پرکردن فیلد های ستاره‌دار اجباری می‌باشد
              </p>
            </div>
          </div>
          <div className="row d-flex align-items-center justify-content-center">
            <form onSubmit={this.handleSubmit}>
              <p className="small text-muted mr-5">
                <span className="bg-white p-3">نقش کاربر</span>
              </p>
              <div className="form-row d-flex justify-content-between border border-gray rounded mx-2 p-3 mt-n4 mb-4">
                <div className="form-group col-md-12">
                  <label>نقش:</label>
                  {this.renderRadioInput("student", "role", "دانش‌آموز", "4")}
                  {this.renderRadioInput("teacher", "role", "معلم", "3")}
                  {this.renderRadioInput("asistant", "role", "معاون", "2")}
                </div>
                {this.state.data.role < 4 &&
                  this.renderInput(
                    "personalCode",
                    "کد پرسنلی*",
                    "col-md-5",
                    "کد پرسنلی را وارد کنید..."
                  )}
                {this.state.data.role === "4" &&
                  this.renderSelect(
                    "section",
                    "مقطع",
                    this.state.section_choices
                  )}
              </div>
              <p className="small text-muted mr-5">
                <span className="bg-white p-3">اطالاعات شخصی</span>
              </p>
              <div className="form-row d-flex justify-content-between border border-gray rounded mx-2 p-3 mt-n4 mb-4">
                {this.renderInput(
                  "firstName",
                  "نام*",
                  "col-md-5",
                  "نام را وارد کنید..."
                )}
                {this.renderInput(
                  "lastName",
                  "نام خانوادگی*",
                  "col-md-5",
                  "نام خانوادگی را وارد کنید..."
                )}
                {this.renderInput(
                  "codeMeli",
                  "کدملی*",
                  "col-md-5",
                  "کدملی را وارد کنید..."
                )}
                {this.renderInput(
                  "shomareShenasname",
                  "شماره شناسنامه*",
                  "col-md-5",
                  "شماره شناسنامه را وارد کنید..."
                )}
                <div className="form-group col-md-5">
                  <label>جنسیت:</label>
                  {this.renderRadioInput("male", "gender", "آقا", "1")}
                  {this.renderRadioInput("female", "gender", "خانم", "0")}
                </div>
                <div className="form-group col-md-5">
                  <label>ملیت:</label>
                  {this.renderRadioInput("iranian", "nation", "ایرانی", "1")}
                  {this.renderRadioInput(
                    "nonIranian",
                    "nation",
                    "اتباع خارجی",
                    "0"
                  )}
                </div>
                <div className="form-group col-md-5 d-flex flex-column">
                  <label>تارخ تولد*</label>
                  <div className="d-flex justify-content-between">
                    {this.renderInput(
                      "dayOfBirth",
                      "",
                      "col-md-4 px-1 mb-0",
                      "روز"
                    )}
                    {this.renderInput(
                      "monthOfBirth",
                      "",
                      "col-md-4 px-1 mb-0",
                      "ماه"
                    )}
                    {this.renderInput(
                      "yearOfBirth",
                      "",
                      "col-md-4 px-1 mb-0",
                      "سال"
                    )}
                  </div>
                </div>
                {this.renderInput(
                  "birthLocation",
                  "محل تولد*",
                  "col-md-5",
                  "محل تولد را وارد کنید..."
                )}
                {this.renderInput(
                  "religion",
                  "مذهب*",
                  "col-md-5",
                  "مذهب را وارد کنید..."
                )}
                <div className="form-group col-md-5">
                  <label>عکس کارت ملی</label>
                  <div className="custom-file">
                    <label
                      className="custom-file-label"
                      htmlFor="cartMeliImg"
                      data-browse="جستجو"
                    >
                      <small>
                        {this.state.uploadData.cartMeliImg.file !== ""
                          ? this.state.uploadData.cartMeliImg.file.name
                          : "آپلود عکس کارت ملی"}
                      </small>
                    </label>
                    <input
                      type="file"
                      onChange={this.uploadCartMeliImg}
                      className="custom-file-input"
                      id="cartMeliImg"
                      name="cartMeliImg"
                    />
                  </div>
                  <div className="form-group">
                    {this.state.uploadData.cartMeliImg.uploadPercentage > 0 && (
                      <React.Fragment>
                        <span
                          className="mr-n4 text-danger text-weight-bold"
                          style={{ position: "absolute" }}
                          onClick={() => {
                            this.cancelImgUpload("cartMeliImg");
                          }}
                        >
                          <i className="far fa-times-circle"></i>
                        </span>
                        <div className="progress" style={{ height: "20px" }}>
                          <div
                            className="progress-bar progress-bar-striped progress-bar-animated"
                            role="progressbar"
                            aria-valuenow={
                              this.state.uploadData.cartMeliImg.uploadPercentage
                            }
                            aria-valuemin="0"
                            aria-valuemax="100"
                            style={{
                              width: `${this.state.uploadData.cartMeliImg.uploadPercentage}%`,
                            }}
                          >
                            {`${this.state.uploadData.cartMeliImg.uploadPercentage}%`}
                          </div>
                        </div>
                      </React.Fragment>
                    )}
                  </div>
                </div>
                <div className="form-group col-md-5">
                  <label>عکس شناسنامه</label>
                  <div className="custom-file">
                    <label
                      className="custom-file-label"
                      htmlFor="shenasnameImg"
                      data-browse="جستجو"
                    >
                      <small>
                        {this.state.uploadData.shenasnameImg.file !== ""
                          ? this.state.uploadData.shenasnameImg.file.name
                          : "آپلود عکس شناسنامه"}
                      </small>
                    </label>
                    <input
                      type="file"
                      onChange={this.uploadShenasnameImg}
                      className="custom-file-input"
                      id="shenasnameImg"
                      name="shenasnameImg"
                    />
                  </div>
                  <div className="form-group">
                    {this.state.uploadData.shenasnameImg.uploadPercentage >
                      0 && (
                      <React.Fragment>
                        <span
                          className="mr-n4 text-danger text-weight-bold"
                          style={{ position: "absolute" }}
                          onClick={() => {
                            this.cancelImgUpload("shenasnameImg");
                          }}
                        >
                          <i className="far fa-times-circle"></i>
                        </span>
                        <div className="progress" style={{ height: "20px" }}>
                          <div
                            className="progress-bar progress-bar-striped progress-bar-animated"
                            role="progressbar"
                            aria-valuenow={
                              this.state.uploadData.shenasnameImg
                                .uploadPercentage
                            }
                            aria-valuemin="0"
                            aria-valuemax="100"
                            style={{
                              width: `${this.state.uploadData.shenasnameImg.uploadPercentage}%`,
                            }}
                          >
                            {`${this.state.uploadData.shenasnameImg.uploadPercentage}%`}
                          </div>
                        </div>
                      </React.Fragment>
                    )}
                  </div>
                </div>
                <div className="form-group col-md-5">
                  <label>عکس 3x4</label>
                  <div className="custom-file">
                    <label
                      className="custom-file-label"
                      htmlFor="personalImg"
                      data-browse="جستجو"
                    >
                      <small>
                        {this.state.uploadData.personalImg.file !== ""
                          ? this.state.uploadData.personalImg.file.name
                          : "آپلود عکس 3x4"}
                      </small>
                    </label>
                    <input
                      type="file"
                      onChange={this.uploadPersonalImg}
                      className="custom-file-input"
                      id="personalImg"
                      name="personalImg"
                    />
                  </div>
                  <div className="form-group">
                    {this.state.uploadData.personalImg.uploadPercentage > 0 && (
                      <React.Fragment>
                        <span
                          className="mr-n4 text-danger text-weight-bold"
                          style={{ position: "absolute" }}
                          onClick={() => {
                            this.cancelImgUpload("personalImg");
                          }}
                        >
                          <i className="far fa-times-circle"></i>
                        </span>
                        <div className="progress" style={{ height: "20px" }}>
                          <div
                            className="progress-bar progress-bar-striped progress-bar-animated"
                            role="progressbar"
                            aria-valuenow={
                              this.state.uploadData.personalImg.uploadPercentage
                            }
                            aria-valuemin="0"
                            aria-valuemax="100"
                            style={{
                              width: `${this.state.uploadData.personalImg.uploadPercentage}%`,
                            }}
                          >
                            {`${this.state.uploadData.personalImg.uploadPercentage}%`}
                          </div>
                        </div>
                      </React.Fragment>
                    )}
                  </div>
                </div>
                {this.renderInput(
                  "homeAddress",
                  "آدرس محل سکونت*",
                  "col-md-5",
                  "ایران-گلستان-گرگان-خیابان شهید بهشتی-کوچه بهشتی 11-پلاک 4-زنگ 2"
                )}
                {this.renderInput(
                  "email",
                  "آدرس پست الکترونیک*",
                  "col-md-5",
                  "example@website.com"
                )}
                {this.renderInput(
                  "homePostalCode",
                  "کد پستی*",
                  "col-md-5",
                  "کدپستی را واردکنید..."
                )}
                {this.renderInput(
                  "homePhoneNumber",
                  "شماره تماس منزل*",
                  "col-md-5",
                  "شماره تماس منزل را وارد کنید..."
                )}
                {this.renderInput(
                  "phoneNumber",
                  "شماره تماس همراه*",
                  "col-md-5",
                  "شماره تماس همراه را وارد کنید..."
                )}
              </div>
              <p className="small text-muted mr-5">
                <span className="bg-white p-3">اطالاعات پدر</span>
              </p>
              <div className="form-row d-flex justify-content-between border border-gray rounded mx-2 p-3 mt-n4 mb-4">
                {this.renderInput(
                  "fatherFirstName",
                  "نام پدر",
                  "col-md-5",
                  "نام پدر را وارد کنید..."
                )}
                {this.renderInput(
                  "fatherPhoneNumber",
                  "شماره تماس همراه پدر",
                  "col-md-5",
                  "شماره تماس همراه پدر را وارد کنید..."
                )}
                {this.renderInput(
                  "fatherCodeMeli",
                  "کدملی پدر",
                  "col-md-5",
                  "کدملی پدر را وارد کنید..."
                )}
                {this.renderInput(
                  "fatherJobName",
                  "شغل پدر",
                  "col-md-5",
                  "شغل پدر را وارد کنید..."
                )}
                {this.renderInput(
                  "fatherWorkAddress",
                  "آدرس محل کار پدر",
                  "col-md-5",
                  "ایران-گلستان-گرگان-خیابان شهید بهشتی-کوچه بهشتی 11-پلاک 4-زنگ 2"
                )}
                {this.renderInput(
                  "fatherWorkPostalCode",
                  "کدپستی محل کار پدر",
                  "col-md-5",
                  "کدپستی محل کار پدر را وارد کنید..."
                )}
                {this.renderInput(
                  "fatherWorkPhoneNumber",
                  "شماره تماس محل کار پدر",
                  "col-md-5",
                  "شماره تماس محل کار پدر را وارد کنید..."
                )}
              </div>
              <p className="small text-muted mr-5">
                <span className="bg-white p-3">اطالاعات مادر</span>
              </p>
              <div className="form-row d-flex justify-content-between border border-gray rounded mx-2 p-3 mt-n4 mb-4">
                {this.renderInput(
                  "motherFirstName",
                  "نام مادر",
                  "col-md-5",
                  "نام مادر را وارد کنید..."
                )}
                {this.renderInput(
                  "motherPhoneNumber",
                  "شماره تماس همراه مادر",
                  "col-md-5",
                  "شماره تماس همراه مادر را وارد کنید..."
                )}
                {this.renderInput(
                  "motherCodeMeli",
                  "کدملی مادر",
                  "col-md-5",
                  "کدملی مادر را وارد کنید..."
                )}
                {this.renderInput(
                  "motherJobName",
                  "شغل مادر",
                  "col-md-5",
                  "شغل مادر را وارد کنید..."
                )}
                {this.renderInput(
                  "motherWorkAddress",
                  "آدرس محل کار مادر",
                  "col-md-5",
                  "ایران-گلستان-گرگان-خیابان شهید بهشتی-کوچه بهشتی 11-پلاک 4-زنگ 2"
                )}
                {this.renderInput(
                  "motherWorkPostalCode",
                  "کدپستی محل کار مادر",
                  "col-md-5",
                  "کدپستی محل کار مادر را وارد کنید..."
                )}
                {this.renderInput(
                  "motherWorkPhoneNumber",
                  "شماره تماس محل کار مادر",
                  "col-md-5",
                  "شماره تماس محل کار مادر را وارد کنید..."
                )}
              </div>
              <div className="form-row d-flex justify-content-center">
                {this.renderButton("ثبت کاربر")}
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default RegisterForm;
