import React, { Component } from "react";
import Table from "../common/table/table";
//import Like from "./common/like";

class UsersTable extends Component {
  columns = [
    { path: "firstName", label: "نام" },
    { path: "lastName", label: "نام خانوادگی" },
    { path: "codeMeli", label: "کدملی" },
    { path: "section.name", label: "مقطع" },
    { path: "role.name", label: "نقش" },
    {
      label: "مدیریت",
      key: "manage",
      content: (
        user // fix it
      ) => (
        <React.Fragment>
          <button
            onClick={() => this.props.onDelete(user)}
            className="btn btn-outline-primary btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-eye d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onDelete(user)}
            className="btn btn-outline-info btn-sm mx-2"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-user-edit d-flex align-self-center justify-content-center py-1"></i>
          </button>
          <button
            onClick={() => this.props.onDelete(user)}
            className="btn btn-outline-danger btn-sm"
            style={{ height: "35px", width: "35px" }}
          >
            <i className="fas fa-trash-alt d-flex align-self-center justify-content-center py-1"></i>
          </button>
        </React.Fragment>
      ),
    },
    // {
    //   key: "like",
    //   content: (movie) => (
    //     <Like liked={movie.liked} onClick={() => this.props.onLike(movie)} />
    //   ),
    // },
  ];
  render() {
    const { users, onSort, sortColumn } = this.props;

    return (
      <Table
        columns={this.columns}
        data={users}
        sortColumn={sortColumn}
        onSort={onSort}
      />
    );
  }
}

export default UsersTable;
